<template>
  <div class="length">
    <br/>
    <h1>{{ msg }}</h1>

    <br/>
    <div class="input-group">
      <input type="text" class="form-control" aria-label="Text input with dropdown button" v-model="originValue" placeholder="">
      <div class="input-group-append">
        <button class="btn btn-outline-secondary dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {{ originUnit }}
        </button>
        <div class="dropdown-menu">
          <a class="dropdown-item" v-on:click="onOriginItemClick('mm')">mm</a>
          <a class="dropdown-item" v-on:click="onOriginItemClick('cm')">cm</a>
          <a class="dropdown-item" v-on:click="onOriginItemClick('m')">m</a>
          <div role="separator" class="dropdown-divider"></div>
          <a class="dropdown-item" v-on:click="onOriginItemClick('in')">in</a>
          <a class="dropdown-item" v-on:click="onOriginItemClick('ft-us')">ft-us</a>
          <a class="dropdown-item" v-on:click="onOriginItemClick('ft')">ft</a>
          <a class="dropdown-item" v-on:click="onOriginItemClick('fathom')">fathom</a>
          <a class="dropdown-item" v-on:click="onOriginItemClick('mi')">mi</a>
          <a class="dropdown-item" v-on:click="onOriginItemClick('nMi')">nMi</a>
        </div>
      </div>
      <button class="btn btn-primary btn-sm">=</button>
      <input type="text" class="form-control" aria-label="Text input with dropdown button" v-model="resultValue" placeholder="">
      <div class="input-group-append">
        <button class="btn btn-outline-secondary dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {{ resultUnit }}
        </button>
        <div class="dropdown-menu">
          <a class="dropdown-item" v-on:click="onResultItemClick('mm')">mm</a>
          <a class="dropdown-item" v-on:click="onResultItemClick('cm')">cm</a>
          <a class="dropdown-item" v-on:click="onResultItemClick('m')">m</a>
          <div role="separator" class="dropdown-divider"></div>
          <a class="dropdown-item" v-on:click="onResultItemClick('in')">in</a>
          <a class="dropdown-item" v-on:click="onResultItemClick('ft-us')">ft-us</a>
          <a class="dropdown-item" v-on:click="onResultItemClick('ft')">ft</a>
          <a class="dropdown-item" v-on:click="onResultItemClick('fathom')">fathom</a>
          <a class="dropdown-item" v-on:click="onResultItemClick('mi')">mi</a>
          <a class="dropdown-item" v-on:click="onResultItemClick('nMi')">nMi</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import convert from 'convert-units'

export default {
  name: 'Length',
  data () {
    return {
      msg: '',
      originValue: 0,
      originUnit: '',
      resultValue: 0,
      resultUnit: ''
    }
  },
  mounted () {
    this.resultValue = convert(this.originValue).from(this.originUnit).to(this.resultUnit)
  },
  methods: {
    onOriginItemClick: function (item) {
      this.originUnit = item
      this.resultValue = convert(this.originValue).from(this.originUnit).to(this.resultUnit)
    },
    onResultItemClick: function (item) {
      this.resultUnit = item
      this.resultValue = convert(this.originValue).from(this.originUnit).to(this.resultUnit)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
</style>



